/**
 *  
 */
function getLocation() {
	return new Promise((resolve, reject) => {
		const that = this;
		uni.getLocation({
			type: 'wgs84',
			complete: res => {
				//  longitude
				//  latitude
				// accuracy
				if (res.errMsg == "getLocation:ok") {
					resolve([0, res]);
					//   
					const data = res || {};
					uni.setStorageSync('userLocation', data);
				} else if (res.errMsg == 'getLocation:fail auth deny' || res.errMsg ==
					'getLocation:fail authorize no response') { //
					resolve([1]); //
				} else if (res.errMsg == 'getLocation:fail:auth denied') { //
					resolve([2]);
				} else { // 
					resolve([3]);
				}
			}
		})
	})
}

/**
 * 
 */
function analysisMobile(session_key, encryptionData) {
	const that = this;
	return new Promise((resolve, reject) => {
		const params = {
			'sessionKey': session_key,
			'encryptedData': encryptionData.encryptedData,
			'iv': encryptionData.iv,
			'provinceCode': 'sdqp'
		};
		uni.request({
			url: 'https://xcxact.vjifen.com/api/decrypt',
			method: 'POST',
			data: params,
			success: res => {
				console.log(res);
				const backData = res.data.data.decryptData;
				uni.setStorage({
					key: 'userMobileData',
					data: backData
				})
				resolve(backData);
			},
			fail: err => {
				uni.showModal({
					title: '',
					content: '' + res.data
				})
				reject('decrypt', err)
			}
		})
	})
}

