//  code 
const getCode = function() {
	const promise = new Promise((resolve, reject) => {
		uni.login({
			success: function(loginRes) {
				if (loginRes && loginRes.code) {
					resolve(loginRes.code)
				} else {
					reject(loginRes)
				}
			}
		});
	}).catch(res => {
		uni.showToast({
			icon: 'none',
			title: res.errMsg || 'code',
			duration: 2000
		});
	})
	return promise
}

// 2 1  0
const getSetting = function() {
	const promise = new Promise((resolve, reject) => {
		uni.getSetting({
			success(res) {
				let authSetting = res.authSetting
				//
				if (authSetting['scope.userInfo']) {
					resolve(1)
					return
				}
				//
				if (authSetting['scope.userInfo'] === false) {
					resolve(0)
					return
				}

				resolve(2)
			},
			fail(res) {
				reject(res)
			}
		})
	}).catch(res => {
		uni.showToast({
			icon: 'none',
			title: res.errMsg || '',
			duration: 2000
		});
	})
	return promise
}

