/**
 *  code  openid session_key
 */
const getOpenid = function(code = '', provinceCode = '') {
	const sendUrl = 'https://xcxact.vjifen.com/api/getOpenid';
	const sendData = {
		code,
		provinceCode
	}
	const promise = new Promise((resolve, reject) => {
		uni.request({
			url: sendUrl,
			method: 'POST',
			data: sendData,
			success: (result) => {
				const backData = result.data;
				if (backData.code == 0) {
					//
					let data = backData.data || {};
					uni.setStorageSync('userData', data);
					resolve(backData.data);
				} else {
					console.log(2)
					resolve(result.msg);
				}
			},
			fail: (error) => {
				reject(error);
			}
		});
	})
	return promise
}


