import { dateformat } from '@/common/basicsFun.js';
// 
function wxScanCode() {
	uni.scanCode({
		success: function(res) {
			console.log('' + res.scanType);
			console.log('' + res.result);
			uni.setStorage({
				key: "codeData",
				data: res
			})
			uni.setStorage({
				key: "sweepstr",
				data: res.result
			})
			const dateStatus = dateformat(config.startDate);
			console.log("dateStatus");
			console.log(dateStatus);
			console.log(config.startDate);
			uni.setStorage({
				key: "dateStatus",
				data: dateStatus == true ? '1' : '0'
			})
			uni.setStorage({
				key: "isInitsweepstr",
				data: 'true'
			})
			getApp().globalData.isInitsweepstr = 'true';
			if (!dateStatus) {
				uni.switchTab({
					url: '../index/index',
					complete() {
						getApp().globalData.openQrcode = false
					}
				})
			} else {
				uni.redirectTo({
					url: '../activityEntrance/activityEntrance?sweepstr=' + encodeURIComponent(res.result)+'&status=1',
					complete() {
						getApp().globalData.openQrcode = false
					}
				})
			}
		},
		fail: function(res) {
			console.log('')
			console.log(res);
			uni.switchTab({
				url: '../index/index',
				complete() {
					getApp().globalData.openQrcode = false
				}
			})
		}
	})
}


